import React from "react";
import axios from "axios";
import Movie from "../components/Movie";
import "./Home.css";

class Home extends React.Component {
  state = {
    isLoading: true,
    movies: [],
    point: "",
  };

  getMovies = async () => {
    this.loadMovies();
    const {
      data: {
        data: { movies },
      },
    } = await axios.get(
      "https://yts-proxy.now.sh/list_movies.json?sort_by=rating"
    );
    /* 
    const {data: {data: {movies}}} = await fetch("https://yts-proxy.now.sh/list_movies.json")
      .then((response) => response.json())
      .then((data) => console.log(data)); 
    여기서 사용한 fetch는 위의 axiox와 같은 역할을 한다. 표현방법만 다를 뿐, 어떤 것을 사용해도 문제 되지 않는다.
    */
    this.setState({ movies, isLoading: false });
  };

  loadMovies() {
    const pointArray = [];
    const point = ".";
    const pointInterval = setInterval(() => {
      pointArray.push(point);
      this.setState({ point: pointArray.join("") });
      if (pointArray.length === 3) {
        pointArray.splice(0, 3);
      }
    }, 300);

    setTimeout(() => {
      clearInterval(pointInterval);
    }, 2000);
  }

  componentDidMount() {
    this.getMovies();
  }

  render() {
    const { isLoading, movies } = this.state;
    return (
      <section className="container">
        {isLoading ? (
          <div className="loader">
            <span className="loader__text">Loading{this.state.point}</span>
          </div>
        ) : (
          <div className="movies">
            {movies.map((movie) => (
              <Movie
                key={movie.id}
                id={movie.id}
                year={movie.year}
                title={movie.title}
                summary={movie.summary}
                poster={movie.medium_cover_image}
                genres={movie.genres}
              />
            ))}
          </div>
        )}
      </section>
    );
  }
}

export default Home;
